<template>
  <div class="container-lg pagebreak">
    <div class="card">
      <div class="card-body">
        <h3 class="row text-start">{{question}}</h3>
        <h5 class="row" v-show="display_answers">{{answer}}</h5>
        <div class="row">
          <div class="input-group mb-3">
            <div class="input-group-text" v-bind:class="getAnswerClass('A')">
              <input type="radio" :name="question" value="A" class="form-check-input mt-0" aria-describedby="label-a" v-model="pick" v-on:change="selectAnswer">
              <span id="label-a" class="text-wrap text-start ms-2">{{a}}</span>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="input-group mb-3">
            <div class="input-group-text" v-bind:class="getAnswerClass('B')">
              <input type="radio" :name="question" value="B" class="form-check-input mt-0" aria-describedby="label-b" v-model="pick" v-on:change="selectAnswer">
              <span id="label-b" class="text-wrap text-start ms-2">{{b}}</span>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="input-group mb-3">
            <div class="input-group-text" v-bind:class="getAnswerClass('C')">
              <input type="radio" :name="question" value="C" class="form-check-input mt-0" aria-describedby="label-c" v-model="pick" v-on:change="selectAnswer">
              <span id="label-c" class="text-wrap text-start ms-2">{{c}}</span>
            </div>
          </div>
        </div>
        <div class="row mb-3">
          <div class="input-group">
            <div class="input-group-text" v-bind:class="getAnswerClass('D')">
              <input type="radio" :name="question" value="D" class="form-check-input mt-0" aria-describedby="label-d" v-model="pick" v-on:change="selectAnswer">
              <span id="label-d" class="text-wrap text-start ms-2">{{d}}</span>
            </div>
          </div>
        </div>
        <!--    <figure class="figure">-->
        <!--      <img src="@/assets/logo.png" class="figure-img img-fluid rounded" alt="...">-->
        <!--      <figcaption class="figure-caption">A caption for the above image.</figcaption>-->
        <!--    </figure>-->
      </div>
    </div>
  </div>
</template>

<script>

import { mapState } from 'vuex'

const regExp1 = /\(([^)]+)\)/;
const regExp2 = /\[(.*?)\]/;

export default {
  name: "ExamQuestion",
  props: {
    question: String,
    answer: String,
    a: String,
    b: String,
    c: String,
    d: String
  },
  data: function() {
    return {
      pick: ''
    }
  },
  computed: {
    correct_answer(){
      return regExp1.exec(this.answer)[1]
    },
    fcc_hint(){
      const fcc_section = regExp2.exec(this.answer)

      if(fcc_section && fcc_section.length > 0){
        const fcc_hints = fcc_section[1].split(',')
        for(const [index, hint] of fcc_hints.entries()){
          fcc_hints[index] = hint.replace(/[^0-9$.,]/g, '')
        }
        return fcc_hints
      }

      return ''
    },
    ...mapState([
      'display_answers'
    ])
  },
  methods: {
    selectAnswer () {
      this.$emit('selectAnswer', {
        answer: this.answer,
        pick: this.pick
      })
    },
    getAnswerClass(val){
      if(this.display_answers){
        return {
          active: true,
          'alert-success': val == this.correct_answer,
          'alert-danger': val != this.correct_answer
        }
      }
    }
  }
}
</script>

<style scoped>

@media print {
  .pagebreak {
    clear: both;
    page-break-inside: avoid;
    break-inside: avoid;
  }
}
</style>