<template>
  <div id="control-panel" class="container-lg d-print-none">
    <nav class="navbar fixed-top navbar-light bg-light">
      <div class="container-lg">
        <span class="navbar-text">
            {{examTitle}}
        </span>
        <span class="navbar-text">
            {{ examDateRange }}
        </span>
        <span class="badge bg-success" v-show="display_answers" v-bind:class="{'bg-success': passed, 'bg-danger': !passed}">{{correct_answers}}/{{total_questions}} {{ passed ? 'Passed' : 'Failed'}}</span>
        <button  v-on:click="gradeExam">Grade</button>
      </div>
    </nav>
  </div>

  <div class="container-lg">
    <section v-for="subelement in activeExamPool['subelements']" :key="subelement" :id="subelement">
      <div v-for="group in activeExamPool[subelement]['section_groups']" :key="group" :id="group">
          <ExamQuestion @selectAnswer="onSelectAnswer" v-for="q in activeExamPool[subelement][group].questions" :key="q" :question="q.question" :answer="q.answer" :a="q.A" :b="q.B" :c="q.C" :d="q.D"></ExamQuestion>
      </div>
    </section>
  </div>
</template>

<script>
import examPool from '@/assets/technical_exam_pool.json';
import ExamQuestion from "@/components/ExamQuestion";
import { mapState } from 'vuex'

const regExp = /\(([^)]+)\)/;

export default {
  name: "TechnicalExam",
  components: {
    ExamQuestion
  },
  data: function() {
    return {
      examPoolData: examPool,
      score_sheet: {},
      total_questions: 0,
      correct_answers: 0,
      passed: false
    }
  },
  methods: {
    generateExam () {
      this.score_sheet = {}
      this.total_questions = 0
      this.correct_answers = 0
      for(let element of this.activeExamPool['subelements']){
        for(let group of this.activeExamPool[element]['section_groups']){
          const question = this.examPoolData[element][group]['questions'][Math.floor(Math.random() * this.examPoolData[element][group]['questions'].length)]
          this.activeExamPool[element][group]['questions'] = [question]
          this.score_sheet[question.answer] = ''
          this.total_questions += 1
        }
      }
    },
    onSelectAnswer (choice) {
      this.score_sheet[choice.answer] = choice.pick
    },
    gradeExam () {
      this.correct_answers = 0;
      for (const property in this.score_sheet) {
        const matches = regExp.exec(property)
        if(matches[1] === this.score_sheet[property]){
          this.correct_answers += 1
        }
      }
      this.$store.commit('setDisplayAnswers', true)
      if(this.correct_answers >= 26){
        this.passed = true
      }else{
        this.passed = false
      }
    }
  },
  computed: {
    examTitle(){
      return this.examPoolData['exam_title']
    },
    examDateRange(){
      return this.examPoolData['exam_effective_daterange']
    },
    activeExamPool() {
      const activeExamPool = JSON.parse(JSON.stringify(this.examPoolData))

      for(let element of activeExamPool['subelements']){
        for(let group of activeExamPool[element]['section_groups']){
          activeExamPool[element][group]['questions'] = []
        }
      }

      return activeExamPool;
    },
    ...mapState([
      'display_answers'
    ])
  },
  created() {
    this.generateExam();
  }
}
</script>

