import { createStore } from 'vuex'
import { createLogger } from 'vue-logger-plugin'

const loggerOptions = {
    isEnabled: true,
    logLevel: 'debug',
    stringifyArguments: false,
    showLogLevel: true,
    showMethodName: true,
    separator: '|',
    showConsoleColors: true
}

const logger = createLogger(loggerOptions)

logger.debug('logger setup')

export default createStore({
    state: {
        display_answers: false,
        score_sheet: []
    },
    mutations: {
        setDisplayAnswers(state, value){
            state.display_answers = value
        }
    },
    actions: {

    },
    modules: {

    }
})